import { Component, Fragment, h, Listen, Prop, State } from '@stencil/core';
import { TTabsState } from './nav-types';
import { ICurrency, IExposedLanguages } from '@/models/common';
import { IExposedProperty } from '@/models/property';

@Component({
  tag: 'ir-nav',
  styleUrl: 'ir-nav.css',
  shadow: true,
})
export class IrNav {
  @Prop() currencies: ICurrency[];
  @Prop() languages: IExposedLanguages[];
  @Prop() logo: string;
  @Prop() website: string;
  @Prop() exposed_property: IExposedProperty;
  @State() currentPage: TTabsState = null;

  private dialogRef: HTMLIrDialogElement;
  private sheetRef: HTMLIrSheetElement;

  handleButtonClick(e: CustomEvent, page: TTabsState) {
    e.stopImmediatePropagation();
    e.stopPropagation();
    this.currentPage = page;
    setTimeout(() => {
      this.dialogRef.openModal();
    }, 50);
  }
  @Listen('closeDialog')
  handleCloseDialog(e: CustomEvent) {
    e.stopImmediatePropagation();
    e.stopPropagation();
    this.dialogRef.closeModal();
  }
  renderDialogBody() {
    switch (this.currentPage) {
      case 'language':
        return <ir-language-picker slot="modal-body" currencies={this.currencies} languages={this.languages}></ir-language-picker>;
      case 'auth':
        return <ir-auth slot="modal-body"></ir-auth>;
      case 'booking_code':
        return <ir-booking-code slot="modal-body"></ir-booking-code>;
      default:
        return null;
    }
  }
  renderLocationField(fieled: string | null, withComma: boolean = true) {
    if (!fieled) {
      return '';
    }
    return withComma ? `, ${fieled}` : fieled;
  }
  render() {
    if (!this.exposed_property) {
      return null;
    }
    return (
      <Fragment>
        <nav class="flex h-14  w-full bg-white items-center justify-between">
          <div class="flex items-center space-x-4">
            <a href={`${this.website?.replace('www.', 'https://')}`}>
              <img src={this.logo} class="object-fill h-10  w-auto"></img>
            </a>
            <div class="hidden md:block">
              <h3 class="font-medium text-lg">{this.exposed_property.name}</h3>
              <p class="text-sm text-slate-700">
                {this.renderLocationField(this.exposed_property.country.name, false)}
                {this.renderLocationField(this.exposed_property.city.name)}
                {this.renderLocationField(this.exposed_property.area)}
                {this.renderLocationField(this.exposed_property.postal)}
              </p>
            </div>
          </div>

          <div class="md:hidden">
            <ir-button variants="icon" onClick={() => this.sheetRef.openSheet()}>
              <p slot="btn-icon" class="sr-only">
                burger menu
              </p>
              <ir-icons slot="btn-icon" name="burger_menu"></ir-icons>
            </ir-button>
          </div>

          <ul class="hidden md:flex items-center gap-2">
            <li>
              <ir-button variants="outline" haveLeftIcon>
                <p slot="left-icon" class="sr-only">
                  home
                </p>
                <ir-icons slot="left-icon" name="home" svgClassName="h-4 w-4"></ir-icons>
              </ir-button>
            </li>
            <li>
              <ir-button variants="outline" label="Booking code" name="booking_code" onButtonClick={e => this.handleButtonClick(e, 'booking_code')}></ir-button>
            </li>
            <li>
              <ir-button variants="outline" label="language" name="language" onButtonClick={e => this.handleButtonClick(e, 'language')}></ir-button>
            </li>
            <li>
              <ir-button variants="outline" label="Login" name="auth" onButtonClick={e => this.handleButtonClick(e, 'auth')}></ir-button>
            </li>
          </ul>
        </nav>
        <ir-sheet ref={el => (this.sheetRef = el)}>
          <ul slot="sheet-content" class="flex flex-col p-4 py-6 md:hidden gap-2">
            <li>
              <ir-button class="w-full" buttonClassName="justify-start" variants="ghost" label="Home" name="home"></ir-button>
            </li>
            <li>
              <ir-button
                class="w-full"
                buttonClassName="justify-start"
                variants="ghost"
                label="Booking code"
                name="booking_code"
                onButtonClick={e => this.handleButtonClick(e, 'booking_code')}
              ></ir-button>
            </li>
            <li>
              <ir-button
                class="w-full"
                buttonClassName="justify-start"
                variants="ghost"
                label="language"
                name="language"
                onButtonClick={e => this.handleButtonClick(e, 'language')}
              ></ir-button>
            </li>
            <li>
              <ir-button
                class="w-full"
                buttonClassName="justify-start"
                variants="ghost"
                label="Login"
                name="auth"
                onButtonClick={e => this.handleButtonClick(e, 'auth')}
              ></ir-button>
            </li>
          </ul>
        </ir-sheet>
        <ir-dialog ref={el => (this.dialogRef = el)}>{this.renderDialogBody()}</ir-dialog>
      </Fragment>
    );
  }
}
